import request from 'superagent'
import jsonp from 'superagent-jsonp'
const state = {
    novel: [],
    reality: [],
    travel: [],
    bookTags: [
        {
        title: '',
        href: 'https://m.douban.com/doulist/35573',
        color: '#42BD56'
        },
        {
        title: '',
        href: 'https://m.douban.com/doulist/38088147',
        color: '#FF4055'
        },
        {
        title: '',
        href: 'https://m.douban.com/doulist/645579',
        color: '#4F9DED'
        },
        {
        line: true
        },
        {
        title: '',
        href: 'https://m.douban.com/doulist/192653',
        color: '#CC3344'
        },
        {
        title: '',
        href: 'https://m.douban.com/doulist/89925',
        color: '#FFAC2D'
        },
        {
        title: '',
        href: 'https://m.douban.com/doulist/45361809',
        color: '#3BA94D'
        }
    ]
}

const mutations = {
    getBook(state,payload){
        switch(payload.tag){
            case 'novel':
            state.novel = payload.res
            break
            case 'reality':
            state.reality = payload.res
            break
            case 'travel':
            state.travel = payload.res
            break
            default:
            state.novel = payload.res
        }
    }
}

const actions = {
    // 3 tag  state
    getBook({commit}){
        request
          .get('https://api.douban.com/v2/book/search?q=&count=8')
          .use(jsonp)
          .end((err,res)=>{
              if(!err){
                commit({type:'getBook',tag:'novel',res: res.body.books})
              }
          })
        request
          .get('https://api.douban.com/v2/book/search?q=&count=8')
          .use(jsonp)
          .end((err,res)=>{
              if(!err){
                commit({type:'getBook',tag:'reality',res: res.body.books})
              }
          })
        request
          .get('https://api.douban.com/v2/book/search?q=&count=8')
          .use(jsonp)
          .end((err,res)=>{
              if(!err){
                commit({type:'getBook',tag:'travel',res: res.body.books})
              }
          })
    }
}

export default {
    state,
    mutations,
    actions
}


